var BaseView = require("BaseView")

cc.Class({
    extends: BaseView,

    properties: {
        moduleName : {
            override: true,
            default: "CreateRoom",
            // type: 'String',
            visible: false,
        },
        
    },

    onLoad: function () {
        this._super()
    },

    start: function () {
        this.mgr.loadRuleData()
    },

    show: function () {
        this._super()
        this.mgr.loadPhpData()
    },

    onCreate: function () {
        this.mgr.requestCreateRoom()
        this.hide()
    },
});
